import http from "../http";



//
const selectConsumedService = (reportData) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-point-lottery/promotion/selectConsumed', { areaCode: reportData }, true).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error)
        })
    });
}

