import http from "../http";



//
const updateRecAddrService = (reportData) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-commerce-sale/address/updateRecAddr',  reportData , false).then(result => {
            console.log(result);
            result.resultCode=='0' ? resolve(result) : reject(result.msg)
        }).catch(error => {
            reject(error)
        })
    });
}



